.OnOffBoardingContainer {
  width: 100%;
  height: auto;
  min-height: calc(100vh);
  overflow: hidden;
  background-color: #fafafa;
  box-shadow: 0 -5px 5px -5px #fafafa; }
  .OnOffBoardingContainer .OnOffBoardingItemContainer {
    margin-bottom: 0; }
    .OnOffBoardingContainer .OnOffBoardingItemContainer .OnOffBoardingItem {
      position: relative;
      width: auto;
      max-width: 400px;
      margin: 0;
      padding: 2em;
      overflow: visible;
      font-weight: bold;
      font-size: 14px;
      text-align: center;
      color: #ffffff; }
      .OnOffBoardingContainer .OnOffBoardingItemContainer .OnOffBoardingItem:not(:last-child):before {
        content: "\e903";
        position: absolute;
        top: 25%;
        left: 50%;
        display: block;
        width: 100%;
        height: 6px;
        font-size: 3.8rem;
        font-family: "real-icons" !important;
        color: #ebebed; }
      .OnOffBoardingContainer .OnOffBoardingItemContainer .OnOffBoardingItem.hasBigFontsize .CardTitle {
        font-size: 18px;
        line-height: 24px !important;
        color: #373f51 !important; }
      .OnOffBoardingContainer .OnOffBoardingItemContainer .OnOffBoardingItem.hasBigFontsize:not(:last-child):before {
        font-size: 3.8rem !important; }

.OnOffBoardingTitle {
  text-align: center; }

.OnOffBoardingSubtitle {
  text-align: center; }

.OnOffBoardingImage {
  width: auto;
  height: 250px;
  padding: 2em; }

.OnOffBoardingButton {
  width: 210px; }

@media (max-width: 600px) {
  .OnOffBoardingContainer .OnOffBoardingItemContainer .OnOffBoardingItem {
    max-width: 100%; } }
