.sidebar {
  position: fixed;
  top: 64px;
  background: #fff;
  box-shadow: 1px 0 20px 0 rgba(0, 0, 0, 0.1);
  height: 100%;
  overflow: hidden;
  width: 80px;
  z-index: 50;
  transition: width 0.3s ease-in-out; }
  @media only screen and (max-width: 48em) {
    .sidebar {
      left: -80px !important; } }
  .sidebar--is-expanded {
    width: 220px;
    left: 0 !important; }
