.listingsDrawerContainer {
  max-width: 70vw !important; }

ul.sortableContainer li.sortableItem {
  position: relative;
  font-size: 16px;
  border-top: 1px solid #d7d9dc;
  cursor: -webkit-grab;
  cursor: grab; }
  ul.sortableContainer li.sortableItem span {
    padding-right: 0.5rem;
    padding-left: 0.5rem; }
  ul.sortableContainer li.sortableItem .address {
    font-size: 14px;
    line-height: 24px;
    font-family: Muli;
    color: #373f51; }
  ul.sortableContainer li.sortableItem .price {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    font-family: Muli;
    color: #0099a7; }
    ul.sortableContainer li.sortableItem .price::after {
      content: ".";
      position: absolute;
      bottom: 24px;
      margin-right: 0.5rem;
      margin-left: 0.5rem;
      font-weight: bold;
      font-size: 35px;
      font-family: Muli;
      color: #737885; }
  ul.sortableContainer li.sortableItem .state {
    margin-left: 1.5rem;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    font-family: Muli;
    color: #737885; }

.thumbnailPhoto {
  border-radius: 5px;
  box-shadow: 0 0 4px 0 #9c9c9c; }
