.StepperContainer {
  position: relative;
  z-index: 100;
  width: 100%;
  height: 96px;
  background-color: #ffffff;
  box-shadow: 0 5px 5px -4px rgba(55, 63, 81, 0.1); }
  .StepperContainer .StepperProgress {
    position: relative;
    display: table;
    width: 70%;
    height: 100%;
    margin-left: 15%;
    table-layout: fixed; }
    .StepperContainer .StepperProgress .step {
      position: relative;
      display: table-cell;
      overflow: visible;
      font-size: 0.9em;
      line-height: 14px;
      font-family: "Muli";
      vertical-align: top;
      text-align: center;
      color: #ffffff; }
      .StepperContainer .StepperProgress .step:not(:last-child):before {
        content: "";
        position: absolute;
        top: 37px;
        left: 50%;
        display: block;
        width: 100%;
        height: 4px;
        background-color: #cacfd3; }
      .StepperContainer .StepperProgress .step .node {
        position: absolute;
        top: 30px;
        left: 50%;
        display: inline-block;
        width: 18px;
        height: 18px;
        background-color: #ffffff;
        border: 2px solid #cacfd3;
        border-radius: 18px; }
      .StepperContainer .StepperProgress .step .progressArrow {
        color: #cacfd3; }
        .StepperContainer .StepperProgress .step .progressArrow:before {
          content: "\e9a9";
          position: absolute;
          top: 5px;
          right: -20px;
          font-size: 1.3rem;
          font-family: "real-icons" !important; }
      .StepperContainer .StepperProgress .step .link,
      .StepperContainer .StepperProgress .step .noLink {
        font-size: 14px;
        color: #cacfd3;
        text-decoration: none; }
      .StepperContainer .StepperProgress .step .link {
        cursor: pointer !important; }
      .StepperContainer .StepperProgress .step.complete:before {
        background-color: #0099a7; }
      .StepperContainer .StepperProgress .step.complete .node {
        background-color: #0099a7;
        border-color: #0099a7; }
        .StepperContainer .StepperProgress .step.complete .node:before {
          content: "\e976";
          position: absolute;
          top: 0;
          right: -2px;
          width: 18px;
          height: 18px;
          font-family: "real-icons" !important; }
      .StepperContainer .StepperProgress .step.complete .progressArrow {
        color: #737885; }
        .StepperContainer .StepperProgress .step.complete .progressArrow:before {
          content: "\e9a9";
          position: absolute;
          top: 5px;
          right: -20px;
          font-size: 1.3rem;
          font-family: "real-icons" !important; }
      .StepperContainer .StepperProgress .step.complete .link {
        font-weight: 500;
        color: #0099a7; }
      .StepperContainer .StepperProgress .step.inProgress:before {
        background: #0099a7;
        /* Old Browsers */
        /* FF3.6+ */
        background: -webkit-gradient(left top, right top, color-stop(0%, #0099a7), color-stop(11%, #0099a7), color-stop(11%, #cacfd3), color-stop(100%, #cacfd3));
        /* Chrome, Safari4+ */
        /* Chrome10+,Safari5.1+ */
        /* Opera 11.10+ */
        /* IE 10+ */
        background: linear-gradient(to right, #0099a7 0%, #0099a7 11%, #cacfd3 11%, #cacfd3 100%);
        /* W3C */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$blue', endColorstr='$neutral06', GradientType=1 );
        /* IE6-9 */ }
      .StepperContainer .StepperProgress .step.inProgress .node {
        background-color: #0099a7;
        border: 3px solid #ffffff;
        border-color: #ffffff;
        box-shadow: 0 0 10px -2px rgba(55, 63, 76, 0.35); }
      .StepperContainer .StepperProgress .step.inProgress .link {
        font-weight: 500;
        color: #0099a7; }
