.mc-dropdown {
  min-width: 200px;
  width: 300px;
  max-height: 500px;
  z-index: 1;
  right: 0;
  top: 22px;
  overflow: initial;
  background: #282b33 !important; }
  .mc-dropdown.arrow-top:after {
    content: ' ';
    position: absolute;
    right: 20px;
    top: -7px;
    border-top: none;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    border-bottom: 10px solid #282b33; }
  @media only screen and (max-width: 440px) {
    .mc-dropdown {
      width: auto;
      right: 0; } }
