* {
  margin: 0; }

.sidebar {
  width: 442px;
  min-width: 442px;
  height: 100vh !important;
  max-height: 100vh;
  overflow-x: hidden;
  box-shadow: -2px 2px 4px 0 rgba(0, 0, 0, 0.2); }
  .sidebar.fixedKelleGuideSideBar {
    position: fixed;
    top: 0;
    right: 0;
    margin-left: 442px; }
  .sidebar .sidebarHeaderContainer {
    border-bottom: 1px solid #d7d9dc; }
  .sidebar .sidebarContainer {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden; }
    .sidebar .sidebarContainer .subContainer {
      position: absolute;
      top: 0;
      right: -20px;
      /* Increase/Decrease this value for cross-browser compatibility */
      bottom: 0;
      left: 0;
      overflow-y: scroll; }
      .sidebar .sidebarContainer .subContainer .subHeaderContainer {
        padding: 1rem !important;
        background-color: #f1f1fa; }
        .sidebar .sidebarContainer .subContainer .subHeaderContainer .ideaImage {
          width: 85px;
          height: auto; }
  .sidebar .sidebarButtonsContainer {
    border-top: 1px solid #ebebed; }
  .sidebar .sideBarBackLink button {
    font-weight: bold;
    color: #0099a7;
    text-decoration: none; }
  .sidebar .sideBarBackLink :hover {
    text-decoration: underline;
    cursor: pointer; }
