.drawer {
  width: 30% !important;
  max-width: 50% !important;
  background: #2f3441 !important; }
  @media only screen and (max-width: 580px) {
    .drawer {
      width: 100% !important;
      max-width: 100% !important; } }
  @media only screen and (max-width: 800px) and (min-width: 581px) {
    .drawer {
      width: 70% !important;
      max-width: 70% !important; } }
  @media only screen and (max-width: 1140px) and (min-width: 801px) {
    .drawer {
      width: 50% !important;
      max-width: 50% !important; } }
  @media only screen and (max-height: 675px) {
    .drawer {
      width: 100% !important;
      max-width: 100% !important;
      overflow: auto !important; } }
  .drawer .drawer__content {
    height: 100vh;
    margin-top: 0 !important;
    padding-top: 10px; }

.drawerWithHelpCenter {
  width: 45% !important; }
  @media only screen and (max-width: 580px) {
    .drawerWithHelpCenter {
      width: 100% !important; } }
