.linegraphBarContainer {
  position: relative;
  height: 20px;
  overflow: hidden; }
  .linegraphBarContainer .linegraphRightDefault {
    display: inline-block;
    height: 14px;
    background-color: #d7d9dc;
    border-radius: 0 10px 10px 0;
    transition: 0.3s ease; }
  .linegraphBarContainer .linegraphLeftDefault {
    display: inline-block;
    height: 14px;
    background-color: #d7d9dc;
    border-radius: 10px 0 0 10px;
    transition: 0.3s ease; }
  .linegraphBarContainer .linegraphLeft {
    display: inline-block;
    height: 14px;
    background-color: #00c392;
    border-radius: 10px 0 0 10px;
    transition: 0.3s ease; }
  .linegraphBarContainer .linegraphMiddle {
    display: inline-block;
    height: 14px;
    background-color: #fc6a59;
    transition: 0.3s ease; }
  .linegraphBarContainer .linegraphRight {
    display: inline-block;
    height: 14px;
    background-color: #7774cf;
    border-radius: 0 10px 10px 0;
    transition: 0.3s ease; }

.linegraphLeftLabel {
  display: inline-block;
  height: 14px;
  transition: 0.3s ease; }

.linegraphMiddleLabel {
  display: inline-block;
  height: 14px;
  transition: 0.3s ease; }

.linegraphRightLabel {
  display: inline-block;
  height: 14px;
  transition: 0.3s ease; }

.sublabel {
  width: 100%;
  font-size: 10px;
  line-height: 18px;
  font-family: "Muli";
  text-align: center;
  color: #afb2b9;
  letter-spacing: 1px; }

.sublabelHeading {
  padding-right: 1rem;
  font-family: "Muli";
  text-align: left; }

.span {
  font-size: 10px;
  line-height: 18px;
  font-family: Muli;
  color: #afb2b9;
  letter-spacing: 1px; }

.linegraphLeftLabelDefault,
.linegraphRightLabelDefault {
  display: inline-block;
  transition: 0.3s ease; }
