.notification:not(:last-child) {
  padding-bottom: 15px; }

.notification .notification-details {
  flex: 2; }
  .notification .notification-details .notification-content {
    width: 100%; }
    .notification .notification-details .notification-content .notification-title {
      font-size: 14px;
      font-weight: bold;
      color: #d7d9dc !important;
      line-height: 1.5; }
    .notification .notification-details .notification-content .notification-message {
      font-size: 12px; }
    .notification .notification-details .notification-content .notification-created-at {
      font-size: 12px;
      color: #cccccc; }

.notification .notification-actions {
  text-align: right;
  position: relative;
  margin-top: 0.5rem; }
  @media only screen and (min-width: 580px) {
    .notification .notification-actions {
      flex: 1; } }
  .notification .notification-actions .spinner {
    text-align: right;
    color: #cccccc;
    position: absolute;
    right: 25px;
    top: -15px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: block;
    z-index: 1;
    font-size: 26px; }

.bg-transparent {
  background: transparent !important; }

.notification-message-link {
  color: #32acb8;
  text-decoration: none; }

.notification-text {
  color: #d7d9dc !important;
  font-size: 14px;
  line-height: 1.3; }
