.roles-list-card {
  min-width: 200px;
  width: 220px;
  max-height: 500px;
  z-index: 1;
  left: -6.6em;
  top: 3em;
  background: #282b33 !important; }
  @media only screen and (max-width: 580px) {
    .roles-list-card {
      width: 300px !important;
      left: -12.8em !important; }
      .roles-list-card div label {
        font-size: 18px !important; } }

.roles-list-card.arrow-top:after {
  content: ' ';
  position: absolute;
  right: 20px;
  top: -7px;
  border-top: none;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #282b33; }
  @media only screen and (max-width: 580px) {
    .roles-list-card.arrow-top:after {
      right: 70px; } }

@media only screen and (max-width: 580px) {
  .mobile-team-selector {
    padding-right: 0px !important; }
  .select-team-icon-desktop {
    display: none; } }

@media only screen and (min-width: 580px) {
  .select-team-icon-mobile {
    display: none; } }

@media only screen and (max-width: 350px) {
  .mobile-team-selector {
    margin-left: -0.6em !important; } }
