.account {
  height: 64px;
  margin-left: 2.5em; }
  @media only screen and (max-width: 580px) {
    .account {
      margin-left: 1.5em;
      margin-right: 1em; } }
  @media only screen and (max-width: 350px) {
    .account {
      margin-left: 0.4em; } }

.account-dropdown {
  box-shadow: 0 1px 10px 0 #ebe8e9;
  width: 200px;
  background: #282b33;
  position: absolute;
  right: 4.4em;
  top: 4.3em; }
  @media only screen and (max-width: 580px) {
    .account-dropdown {
      width: 250px;
      right: 1em; } }

.account-dropdown.arrow-top:after {
  content: ' ';
  position: absolute;
  right: 20px;
  top: -7px;
  border-top: none;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #282b33; }

@media only screen and (min-width: 580px) {
  .help_mobile {
    display: none !important; }
  .market-place-icon-mobile {
    display: none !important; } }

@media only screen and (max-width: 580px) {
  .profile-desktop {
    display: none !important; } }

@media only screen and (min-width: 580px) {
  .profile-mobile {
    display: none !important; } }

.options {
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  height: 40px;
  text-decoration: none !important; }
  @media only screen and (max-width: 580px) {
    .options {
      font-size: 18px !important; } }

.option_column:hover {
  background: #000000 !important; }

.help--logo {
  border-radius: 50%;
  border: 2px solid #cdd1d7; }
  .help--logo:hover {
    border: 2px solid white; }
