.viewport {
  padding-top: 64px;
  height: 100%;
  box-sizing: border-box; }
  @media only screen and (min-width: 48em) {
    .viewport {
      padding: 64px 0 0 220px;
      transition: padding-left 0.3s; } }

@media only screen and (min-width: 48em) {
  :global .viewport--is-expanded {
    padding-left: 80px; } }
