.background {
  height: auto;
  min-height: 120px; }

.w25 {
  width: 25% !important; }

.w80 {
  width: 75% !important; }

.fadedOpacity {
  opacity: 0.4; }

.avatar {
  max-width: 140px;
  border: solid 4px #33adb9;
  border-radius: 50%; }

.dba {
  max-width: 140px; }

.line {
  width: 60px;
  height: 1px;
  border: 1px solid #96cde0; }

.vertLine {
  width: 1px;
  height: 60px;
  margin-right: 0.8rem;
  margin-left: 0.8rem;
  border: 1px solid #96cde0; }

.aboutMe {
  font-size: 1rem;
  letter-spacing: 0.15rem; }

.contactText {
  font-size: 1rem;
  letter-spacing: 0.3rem; }

.icon {
  margin-right: 1rem;
  color: #33adb9; }

.contactContainer {
  height: 400px;
  min-height: 400px;
  max-height: 400px;
  padding: 6% 20%;
  background-color: #343342; }

.input {
  color: white;
  background-color: #5c5c67;
  border: none; }

.input::-webkit-input-placeholder {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.input::-moz-placeholder {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.input:-ms-input-placeholder {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.input::-ms-input-placeholder {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.input::placeholder {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.fakeIconDot {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6); }

.yourTeamLogoContainer {
  padding: 1rem 2rem;
  border: 2px dashed #d7d9dc; }

.currentStepBorder {
  border: 6px dashed #0099a7; }

.profilePhoto {
  width: auto;
  max-width: 250px;
  height: 250px;
  border: 5px solid #ee0033;
  border-radius: 50%; }

.letsTalkFormBackground {
  background-color: #373f51; }

.greyBackground {
  color: #ffffff;
  background-color: #afb2b9;
  border-color: #afb2b9; }

.divider {
  width: 2px;
  margin: 6px 0;
  background: #afb2b9; }

.socialMediaLink > a {
  color: #373f51;
  text-decoration: none; }

.separator div::after {
  content: "";
  position: relative;
  top: 15px;
  left: 40px;
  display: block;
  width: 78px;
  height: 1px;
  border: solid 1px #d22c60;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%); }

.downloadAppButton {
  background-color: #ee0033;
  border-color: #ee0033; }

.downloadAppButton a {
  font-size: 1.1rem;
  color: #ffffff;
  text-decoration: none; }

.AgentBioBodyText {
  min-height: 8rem;
  word-break: break-word; }

.AgentNameHeading {
  letter-spacing: 0.1rem;
  word-break: break-all; }

.AgentBioTopBanner {
  word-break: break-all; }

.blackCover {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.4s; }

.selectedBorder {
  z-index: 5 !important;
  background-color: white;
  border: 4px dashed #64bbc2;
  box-shadow: 1px 4px 5px rgba(0, 0, 0, 0.3);
  transition: 0.4s; }

.MarketinProfileContainer {
  width: 100% !important; }
