.product-select {
  min-width: 200px;
  width: 220px;
  max-height: 500px;
  z-index: 1;
  left: 2.4em;
  top: 4em;
  background: #282b33 !important; }

.product-select.arrow-top:after {
  content: ' ';
  position: absolute;
  right: 40px;
  top: -7px;
  border-top: none;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #282b33; }

.product {
  transition: opacity 0.3s; }

.product--current {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1); }

.product:not(.product--current):hover {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1); }

@media only screen and (max-width: 48em) {
  .product--no-context,
  .product:not(.product--current) {
    display: none !important; } }

@media only screen and (min-width: 769px) {
  .kwlogo-mobile {
    display: none; } }

@media only screen and (max-width: 768px) {
  .kwlogo-desktop {
    display: none; } }
