.bodyDetail {
  overflow: hidden;
  overflow-y: auto;
  height: 90vh;
  padding: 0 5%;
  padding-bottom: 200px;
  font-family: 'Muli', sans-serif; }
  .bodyDetail img {
    max-width: 100%; }
  .bodyDetail a {
    background: transparent !important;
    color: #afb2b9 !important; }
  .bodyDetail span {
    color: #afb2b9 !important;
    background: transparent !important; }
  .bodyDetail table {
    width: 100% !important;
    border-collapse: collapse;
    color: #282b33 !important; }
    .bodyDetail table tbody tr:nth-child(even) {
      background-color: #f5f5f6; }
    .bodyDetail table span {
      color: #282b33 !important; }
    .bodyDetail table th,
    .bodyDetail table td {
      padding: 0.5rem 1rem;
      text-align: center; }

.bodyDetail::-webkit-scrollbar {
  width: 0px; }
