.breadcrumb-item {
  display: inline-flex;
  list-style: none;
  margin: 0 10px;
  margin-right: 0;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  color: #878c97;
  font-size: 14px; }
  .breadcrumb-item:first-child {
    margin-left: 0; }
  .breadcrumb-item:last-child {
    color: #afb2b9 !important;
    font-weight: bold; }
    .breadcrumb-item:last-child .icon {
      display: none; }
  .breadcrumb-item .icon {
    margin-left: 10px; }
