.actionButtons {
  display: flex;
  justify-content: space-between; }
  .actionButtons a {
    color: #d0411f; }

.searchBar {
  background: #373f51; }

.seachBarSelect {
  width: 50%;
  text-transform: capitalize; }
  .seachBarSelect :global .select {
    width: 100%; }
    .seachBarSelect :global .select__control {
      background: #373f51; }
      .seachBarSelect :global .select__control .select__value-container .select__single-value {
        color: white; }
    .seachBarSelect :global .select__placeholder {
      color: white; }

.loader-wrapper {
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: center; }

.results-label {
  font-size: 13px;
  padding: 5px 0;
  background: #fff;
  z-index: 1;
  position: relative; }

.results-wrapper {
  overflow-y: auto;
  height: 53vh;
  padding-bottom: 10vh; }
  @media only screen and (max-width: 440px) {
    .results-wrapper {
      height: 35vh; } }

.results-wrapper::-webkit-scrollbar {
  width: 5px;
  background-color: #f5f5f5; }

.results-wrapper::-webkit-scrollbar-thumb {
  background-color: #373f51; }

.results-notfound-wrapper {
  width: 100%;
  text-align: center;
  padding: 20px; }

.article {
  cursor: pointer; }
  .article__divider {
    border-color: #cacfd3; }
  .article__date {
    text-align: right; }
  .article__image__wrapper {
    margin: 1rem;
    margin-left: -5px;
    margin-top: -5px; }
