.drawer__head {
  display: flex;
  justify-content: space-between;
  height: 72px;
  background: black; }
  .drawer__head__image {
    padding: 10px;
    margin-left: 5px; }
    .drawer__head__image img {
      width: 55px; }
  .drawer__head--end, .drawer__head--start {
    width: 100%;
    display: flex;
    align-items: center; }
  .drawer__head--start {
    justify-content: flex-start; }
  .drawer__head--end {
    justify-content: flex-end;
    width: 300px; }
  .drawer__head .icon {
    color: #afb2b9;
    cursor: pointer;
    transition: color 0.3s;
    position: relative;
    top: 0;
    right: 0;
    padding: 15px 25px;
    font-size: 1.5rem; }
    .drawer__head .icon.drawer__external:after {
      border-right: 2px solid #afb2b9;
      content: '';
      position: absolute;
      right: 0;
      top: 36%;
      border: 0.5px solid #afb2b9;
      height: 30%; }
