.donutchart-track {
  fill: transparent;
  stroke: #dae2e5;
  stroke-width: 26; }

.donutchart-indicator {
  transition: 0.3s ease;
  fill: transparent;
  stroke: #009688;
  stroke-dasharray: 0 10000;
  stroke-width: 26; }

.donutchart {
  display: block;
  margin: 0 auto;
  border-radius: 50%; }

.w100 {
  width: 100%; }

.dot {
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.2rem;
  border-radius: 0.4rem; }

.LegendLabel {
  font-weight: 800;
  font-size: 14px;
  line-height: 20px;
  font-family: "Muli"; }

.LegendValue {
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  font-family: "Muli";
  color: #373f51; }

.verticalSublabelLegend {
  font-size: 14px;
  line-height: 20px;
  font-family: "Muli";
  color: #373f51;
  letter-spacing: 0.2px; }
