/* these came from shell.js */
html {
  height: 100%;
}
body {
  background: #f8f8f8;
  margin: 0;
  height: 100%;
}
#__next {
  height: 100%;
}

/* this came from HelpDrawer.js */
.help-drawer .drawer__content {
  height: 100%;
  margin-top: 0;
}

/* these came from Widget.js */
.zEWidget-launcher {
  /* Make sure zEWidget is totally hidden to user */
  display: none;
}
#webWidget {
  width: 30% !important;
  height: 100% !important;
}

@media only screen and (max-width: 440px) {
  #webWidget {
    width: 100% !important;
  }
}
@media only screen and (max-height: 840px) {
  #webWidget {
    width: 100% !important;
  }
}
